.d-flex{
    justify-content: center;

}

.left{
    padding-top: 20%;
}

.brand-name{
    border-radius: 4px;
    font-family:sans-serif;
    font: bold;
    color: blue;
}

.btn-get-started{
    font-weight: 500;
    text-decoration:none;
    font-size: 16px;
    letter-spacing: 1px;
    padding: 10px 20px;
    border-radius: 50px;
    display: inline-block;
    color:#3498db;
    background-color:white;
    border: 2px solid #3498db;
    transition: 0.5s;
}

.header-img{
    text-align: center;
}

#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
0% {
    transform: translateY(10px)
}
100% {
    transform: translateY(-10px);
}
}

@keyframes up-down {
    0% {
        transform: translateY(10px)
    }
    100% {
        transform: translateY(-10px);
    }
    }

.btn-get-started:hover{
    background:#3498db;
    color: #fff;
}

.img-fluid{
    width: 100%;
    margin-top: 30px;
    height: 70vh;
    border-radius: 20px;
}