@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
.d-flex{
    justify-content: center;

}

.left{
    padding-top: 20%;
}

.brand-name{
    border-radius: 4px;
    font-family:sans-serif;
    font: bold;
    color: blue;
}

.btn-get-started{
    font-weight: 500;
    text-decoration:none;
    font-size: 16px;
    letter-spacing: 1px;
    padding: 10px 20px;
    border-radius: 50px;
    display: inline-block;
    color:#3498db;
    background-color:white;
    border: 2px solid #3498db;
    transition: 0.5s;
}

.header-img{
    text-align: center;
}

#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px)
}
100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
}

@keyframes up-down {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px)
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    }

.btn-get-started:hover{
    background:#3498db;
    color: #fff;
}

.img-fluid{
    width: 100%;
    margin-top: 30px;
    height: 70vh;
    border-radius: 20px;
}
*{
    margin: 0;
    padding: 0;
   box-sizing: border-box;
   font-family: 'Mulish', sans-serif;
}



.navbar{
  background-color: white;
}

.navbar-light{
    color: white;
}

.navbar-brand
{
    color:#fa0b0b;
    font-size: 3.0rem !important;
    font-weight: bold;
}
.navbar-brand:hover{
    color: blue !important;
}

.navbar a{
    color:black;
    font-size: 1.3rem;
    text-transform: capitalize;
}

.nav-item :hover{
color: blue !important;
}

.menu_active{
font-weight: bold;
border-bottom: 1px solid #08f644;
}

.navbar a:hover
{
color:blue;
border-radius: 10px;
}

@media (max-width: 991px){
    .navbar-brand{
        font-size: 1.5rem;
    }

.menu_active
{
    font-weight: bold;
    border-bottom: none;
}
}

.nav-link hover{
    color: blue;
}
